<template>
  <v-form ref="form" @submit.prevent>
    <v-container>
      <v-subheader>Administrador</v-subheader>
      <v-row align="center">
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="administrador.cdUsuario"
            label="Id"
            outlined
            disabled
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center">
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="administrador.nome"
            :counter="50"
            label="Nome"
            placeholder="Nome do Administrador"
            outlined
            required
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center">
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="administrador.login"
            :counter="50"
            label="Login"
            placeholder="Login do Administrador"
            outlined
            required
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center">
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="administrador.senha"
            :counter="50"
            :type="fldPassword ? 'text' : 'password'"
            :append-icon="fldPassword ? 'mdi-eye' : 'mdi-eye-off'"
            hint="Pelo menos 3 caracteres"
            name="fldSenha"
            label="Senha"
            placeholder="Senha do Administrador"
            outlined
            required
            @click:append="fldPassword = !fldPassword"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col class="d-flex" cols="12">
          <v-slider
            v-model="administrador.permissoes"
            min="1"
            max="5"
            thumb-label="always"
            label="Permissões"></v-slider>
        </v-col>
      </v-row>
      <v-btn color="success" class="" @click="save">Salvar</v-btn>
    </v-container>
  </v-form>
</template>

<script>
import DataService from "../service/AdministradorDataService";

export default {
  name: "Administrador",
  data() {
      return {
          administrador: {cdUsuario:0,nome:'',login:'',senha:'',permissoes:0},
          fldPassword: false,
          rules: {
            required: value => !!value || 'Required.',
          },
      };
  },
  computed: {
      id() {
          return this.$route.params.id;
      }
  },
  methods: {
      refresh() {
          if(this.id > 0){
              DataService.retrieve(this.id)
                  .then(response => {
                  this.administrador = response.data
              });
          }
      },
      save(e) {
          e.preventDefault();
          if(this.id > 0){
              DataService.update(this.id, this.administrador, {
                  id: this.id,
                  administrador: this.administrador
              }).then(() => {
                  this.$router.push('/administradores')
              }).catch(e => {
                  console.log(e)
                  if(e.response){
                    this.$store.dispatch('setSnackbar', {
                      showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                    });
                  }
              });
          } else {
              console.log(this.administrador);
              DataService.create(this.administrador, {
                  administrador: this.administrador
              }).then(() => {
                  this.$router.push('/administradores')
              }).catch(e => {
                  console.log(e)
                  if(e.response){
                    this.$store.dispatch('setSnackbar', {
                      showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                    });
                  }
              });
          }
      }
  },
  created() {
      this.refresh()
  }
};
</script>

